<template>
  <v-app class="loginWeb">
    <v-main>
      <v-container style="min-height: 100vh" >
        <div class="row justify-center">
          <div class="col-md-6 text-center mb-5 mt-3">
            <h2 class="heading-section white--text" style="background-color: rgba(0,0,0,0.5)">نام نویسی کنید</h2>
          </div>
        </div>
        <div class="row justify-center">
          <div class="col-md-6 col-lg-5 mb-12">
            <div class="login-wrap p-4 pa-md-5">
              <p style="color: #484848">ثبت نام کرده اید ؟ <router-link :to="{name:'Login'}" tag="a" v-slot="{navigate,href}"><a @click="navigate" :href="href" >وارد شوید</a></router-link></p>

              <form action="#" class="login-form">
                <div class="form-group">
                  <v-text-field
                      type="text"
                      outlined
                      class="rounded-0"
                      @keypress.enter="signup()"
                      :disabled="loading"
                      @input="$v.email.$touch()"
                      @blur="$v.email.$touch()"
                      :error-messages="emailErrors"
                      v-model="email"
                      label="ایمیل"
                      placeholder="مثال : fatika@gmail.com"
                      background-color="rgba(0,0,0,.05)"
                  >

                  </v-text-field>
                  <v-text-field
                      type="text"
                      outlined
                      class="rounded-0"
                      @keypress.enter="signup()"
                      :disabled="loading"
                      :rules="[usernameRules.pattern]"
                      :error-messages="usernameErrors"
                      v-model="username"
                      @input="$v.username.$touch()"
                      @blur="$v.username.$touch()"
                      label="نام کاربری"
                      background-color="rgba(0,0,0,.05)"
                  >

                  </v-text-field>
                </div>
                <div class="form-group">
                  <v-text-field
                      outlined
                      class="rounded-0"
                      @keypress.enter="signup()"
                      :disabled="loading"
                      :error-messages="passErrors"
                      v-model="password"
                      @input="$v.password.$touch()"
                      @blur="$v.password.$touch()"
                      :type="passshow ? 'text' : 'password'"
                      label="کلمه عبور"
                      background-color="rgba(0,0,0,.05)"
                  >
                    <template  v-slot:append>
                      <v-btn v-if="!passshow" @click="passshow= !passshow" icon>
                        <v-icon>mdi-eye-outline</v-icon>
                      </v-btn>
                      <v-btn v-else @click="passshow= !passshow" icon>
                        <v-icon>mdi-eye-off-outline</v-icon>
                      </v-btn>
                    </template>

                  </v-text-field>

                  <v-text-field
                      outlined
                      class="rounded-0"
                      @keypress.enter="signup()"
                      :disabled="loading"
                      v-model="repeatPassword"
                      :error-messages="samepassErrors"
                      @input="$v.repeatPassword.$touch()"
                      @blur="$v.repeatPassword.$touch()"
                      :type="passshow2 ? 'text' : 'password'"
                      label="تکرار کلمه عبور"
                      background-color="rgba(0,0,0,.05)"
                  >
                    <template  v-slot:append>
                      <v-btn v-if="!passshow2" @click="passshow2= !passshow2" icon>
                        <v-icon>mdi-eye-outline</v-icon>
                      </v-btn>
                      <v-btn v-else @click="passshow2= !passshow2" icon>
                        <v-icon>mdi-eye-off-outline</v-icon>
                      </v-btn>
                    </template>

                  </v-text-field>

                  <v-select
                      outlined
                      class="rounded-0"
                      @keypress.enter="signup()"
                      :disabled="loading"
                      item-text="text"
                      :items="[{text:'کاربر عادی',id:3},{text:'طراح',id:4},{text:'کارفرما',id:5}]"
                      item-value="id"
                      v-model="role"
                      label="انتخاب نقش"
                      background-color="rgba(0,0,0,.05)"
                  >

                  </v-select>
                </div>
                <div class="form-group mb-5 d-flex justify-space-between">
                  <div class="w-50">
<!--                    <a href="#">رمز عبور خود را فراموش کرده اید ؟</a>-->
                  </div>
                </div>
                <div class="form-group">
                  <v-btn :loading="loading" :disabled="loading" @click="signup()" height="52"
                         class="btn btn-login rounded-0 p-3 px-5">ثبت نام
                  </v-btn>
                </div>
              </form>
            </div>
          </div>
        </div>
      </v-container>
    </v-main>
    <v-footer class="pa-0 ma-0">
      <FooterApp/>
    </v-footer>
  </v-app>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, sameAs, minLength,email } from 'vuelidate/lib/validators'
import axios from "axios";
import FooterApp from "@/Footer";

export default {
  name: "registerWeb",
  components: {FooterApp},
  mixins : [validationMixin],
  data() {
    return {
      username: null,
      password: null,
      email : null,
      repeatPassword : null,
      role : 3,
      loading: false,
      data: null,
      usernameRules: {
        pattern: value => {
          const pattern = /^(?=.{6,20}$)(?![_.0-9])(?!.*[_.]{2})[a-zA-Z0-9._]+(?<![_.])$/
          return pattern.test(value) || 'نام کاربری باید حاوی حروف مجاز و حداقل 6 کاراکتر باشد'
        },
      },
      passshow: false,
      passshow2: false,
    }
  },
  computed : {
    passErrors () {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.minLength && errors.push('پسورد باید دارای حداقل 6 کاراکتر باشد')
      !this.$v.password.required && errors.push('پسورد را وارد کنید')
      return errors
    },
    samepassErrors () {
      const errors = []
      if (!this.$v.repeatPassword.$dirty) return errors
      !this.$v.repeatPassword.sameAsPassword && errors.push('تکرار پسورد همخوانی ندارد')
      return errors
    },
    usernameErrors () {
      const errors = []
      if (!this.$v.username.$dirty) return errors
      !this.$v.username.required && errors.push('نام کاربری را وارد کنید')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('ایمیل را به شکل صحیح وارد کنید')
      !this.$v.email.required && errors.push('ایمیل را وارد کنید')
      return errors
    },
    usernameValidate() {
      return this.usernameRules.pattern(this.username) === true;
    },
  },
  validations: {
    password: {
      required,
      minLength: minLength(6)
    },
    repeatPassword: {
      sameAsPassword: sameAs('password')
    },
    username:{
      required,
    },
    email: {
      required,
      email
    },

  },
  methods: {
    signup(){
      this.$v.$touch();
      if (this.$v.$invalid || !this.usernameValidate) {
        //
      } else {
        this.loading = true;
        axios.post(this.$store.state.api + 'register/', {
          username : this.username,
          email : this.email,
          password : this.password,
          role : this.role,
        }).then(() => {
          this.$store.state.notify.color = 'success';
          this.$store.state.notify.is = true;
          this.$store.state.notify.text = "ثبت نام شما انجام شد.";
          this.$router.push('/login')
        }).catch(error=>{
          this.errorHandle(error.response,true)
        }).finally(()=>{
          this.loading = false;
        });
      }
    }
  }
}
</script>

